import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { 
  TrendingUp, 
  Users, 
  DollarSign, 
  Package, 
  Star,
  Calendar,
  BarChart3,
  LogOut
} from 'lucide-react';

const AdminDashboard: React.FC = () => {
  const navigate = useNavigate();
  const [timeframe, setTimeframe] = useState('today');

  // Mock analytics data
  const analytics = {
    revenue: {
      today: 15420,
      week: 89650,
      month: 324780,
    },
    orders: {
      today: 45,
      week: 267,
      month: 1024,
    },
    customers: {
      today: 89,
      week: 523,
      month: 2145,
    },
    rating: 4.6,
  };

  const recentOrders = [
    { id: 'ORD001', table: 4, amount: 531, status: 'completed', time: '2 min ago' },
    { id: 'ORD002', table: 2, amount: 378, status: 'preparing', time: '15 min ago' },
    { id: 'ORD003', table: 6, amount: 695, status: 'ready', time: '22 min ago' },
    { id: 'ORD004', table: 1, amount: 234, status: 'served', time: '35 min ago' },
  ];

  const topItems = [
    { name: 'Paneer Butter Masala', orders: 23, revenue: 6555 },
    { name: 'Margherita Pizza', orders: 18, revenue: 5130 },
    { name: 'Hyderabadi Biryani', orders: 15, revenue: 5175 },
    { name: 'Cheese Vada Pav', orders: 12, revenue: 1020 },
  ];

  const handleLogout = () => {
    navigate('/admin/login');
  };

  const StatCard: React.FC<{
    title: string;
    value: string | number;
    icon: React.ElementType;
    color: string;
    change?: string;
  }> = ({ title, value, icon: Icon, color, change }) => (
    <div className="card p-6">
      <div className="flex items-center justify-between">
        <div>
          <p className="text-sm font-medium text-gray-600">{title}</p>
          <p className="text-2xl font-bold text-gray-900">{value}</p>
          {change && (
            <p className="text-sm text-green-600 mt-1">
              {change}
            </p>
          )}
        </div>
        <div className={`w-12 h-12 rounded-lg ${color} flex items-center justify-center`}>
          <Icon className="w-6 h-6 text-white" />
        </div>
      </div>
    </div>
  );

  return (
    <div className="min-h-screen bg-background-light">
      {/* Header */}
      <div className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-2xl font-bold text-gray-900">Admin Dashboard</h1>
              <p className="text-gray-600">Monitor your restaurant performance</p>
            </div>
            
            <div className="flex items-center space-x-4">
              <select
                value={timeframe}
                onChange={(e) => setTimeframe(e.target.value)}
                className="input-field w-auto"
              >
                <option value="today">Today</option>
                <option value="week">This Week</option>
                <option value="month">This Month</option>
              </select>
              
              <button
                onClick={handleLogout}
                className="btn-secondary flex items-center"
              >
                <LogOut className="w-4 h-4 mr-2" />
                Logout
              </button>
            </div>
          </div>
        </div>
      </div>

      <div className="max-w-7xl mx-auto px-4 py-6">
        {/* Stats Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
          <StatCard
            title="Revenue"
            value={`₹${analytics.revenue[timeframe as keyof typeof analytics.revenue].toLocaleString()}`}
            icon={DollarSign}
            color="bg-green-500"
            change="+12% from yesterday"
          />
          
          <StatCard
            title="Orders"
            value={analytics.orders[timeframe as keyof typeof analytics.orders]}
            icon={Package}
            color="bg-blue-500"
            change="+8% from yesterday"
          />
          
          <StatCard
            title="Customers"
            value={analytics.customers[timeframe as keyof typeof analytics.customers]}
            icon={Users}
            color="bg-purple-500"
            change="+15% from yesterday"
          />
          
          <StatCard
            title="Rating"
            value={`${analytics.rating}/5`}
            icon={Star}
            color="bg-yellow-500"
            change="Above average"
          />
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mb-8">
          {/* Recent Orders */}
          <div className="card p-6">
            <div className="flex items-center justify-between mb-4">
              <h3 className="text-lg font-semibold text-gray-900">Recent Orders</h3>
              <Calendar className="w-5 h-5 text-gray-400" />
            </div>
            
            <div className="space-y-3">
              {recentOrders.map((order) => (
                <div key={order.id} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                  <div>
                    <p className="font-medium text-gray-900">{order.id}</p>
                    <p className="text-sm text-gray-600">Table {order.table} • {order.time}</p>
                  </div>
                  <div className="text-right">
                    <p className="font-medium text-gray-900">₹{order.amount}</p>
                    <span className={`text-xs px-2 py-1 rounded-full ${
                      order.status === 'completed' ? 'bg-green-100 text-green-800' :
                      order.status === 'preparing' ? 'bg-blue-100 text-blue-800' :
                      order.status === 'ready' ? 'bg-orange-100 text-orange-800' :
                      'bg-gray-100 text-gray-800'
                    }`}>
                      {order.status}
                    </span>
                  </div>
                </div>
              ))}
            </div>
          </div>

          {/* Top Items */}
          <div className="card p-6">
            <div className="flex items-center justify-between mb-4">
              <h3 className="text-lg font-semibold text-gray-900">Top Selling Items</h3>
              <BarChart3 className="w-5 h-5 text-gray-400" />
            </div>
            
            <div className="space-y-3">
              {topItems.map((item, index) => (
                <div key={item.name} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                  <div className="flex items-center">
                    <div className="w-8 h-8 bg-primary-100 rounded-full flex items-center justify-center mr-3">
                      <span className="text-sm font-bold text-primary-600">{index + 1}</span>
                    </div>
                    <div>
                      <p className="font-medium text-gray-900">{item.name}</p>
                      <p className="text-sm text-gray-600">{item.orders} orders</p>
                    </div>
                  </div>
                  <p className="font-medium text-gray-900">₹{item.revenue.toLocaleString()}</p>
                </div>
              ))}
            </div>
          </div>
        </div>

        {/* Chart Placeholder */}
        <div className="card p-6">
          <h3 className="text-lg font-semibold text-gray-900 mb-4">Revenue Trend</h3>
          <div className="h-64 bg-gray-50 rounded-lg flex items-center justify-center">
            <div className="text-center">
              <TrendingUp className="w-12 h-12 text-gray-400 mx-auto mb-2" />
              <p className="text-gray-600">Revenue chart would be displayed here</p>
              <p className="text-sm text-gray-500">Integrate with Chart.js or similar library</p>
            </div>
          </div>
        </div>

        {/* Quick Actions */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mt-8">
          <button
            onClick={() => navigate('/kitchen')}
            className="card p-4 hover:shadow-card-hover transition-shadow text-left"
          >
            <Package className="w-8 h-8 text-blue-600 mb-2" />
            <h4 className="font-semibold text-gray-900">Kitchen Dashboard</h4>
            <p className="text-sm text-gray-600">Monitor order preparation</p>
          </button>
          
          <button
            onClick={() => navigate('/tables')}
            className="card p-4 hover:shadow-card-hover transition-shadow text-left"
          >
            <Users className="w-8 h-8 text-green-600 mb-2" />
            <h4 className="font-semibold text-gray-900">Customer View</h4>
            <p className="text-sm text-gray-600">See customer interface</p>
          </button>
          
          <div className="card p-4 text-left opacity-75">
            <Star className="w-8 h-8 text-yellow-600 mb-2" />
            <h4 className="font-semibold text-gray-900">Feedback Analysis</h4>
            <p className="text-sm text-gray-600">Coming soon...</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AdminDashboard; 